html {
  box-sizing: border-box;
  overflow-x: hidden; }

body {
  overflow-x: inherit;
  color: #262626;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5; }

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0; }

a {
  color: inherit;
  text-decoration: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.25;
  font-weight: 500;
  font-family: inherit; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3rem; }

h3 {
  font-size: 2.5rem; }

h4 {
  font-size: 2rem; }

h5 {
  font-size: 1.5rem; }

h6 {
  font-size: 1rem; }

input {
  color: currentColor;
  border: 1px solid currentColor;
  padding: .5em; }

input::placeholder {
  color: currentColor; }

input {
  font: 1rem/1.5 "Helvetica Neue", Helvetica, Arial, sans-serif; }

